<template>
  <div class="vm-chart-bar vm-panel">
    <div class="panel-body" :id="this.id" :style="{ height: height + 'px'}">
    </div>
  </div>
</template>
<script>
  import chartTheme from '@/theme/chartTheme.js'
  //  ECharts 
  var echarts = require('echarts/lib/echarts')
  // 
  require('echarts/lib/chart/pie')
  // 
  require('echarts/lib/component/tooltip')
  require('echarts/lib/component/title')
  require('echarts/lib/component/legend')

  export default {
    name: 'VmChartPie',
    props: {
      // 
      title: {
        type: String,
        default: ''
      },
      height: {
        type: Number,
        default: 400
      },
      // , ecahrts
      color: {
        type: Array,
        default: function () {
          return chartTheme.color
        }
      },
      // 
      bgColor: {
        type: String,
        default: '#fff'
      },
      // 
      data: {
        type: Array,
        required: true,
        default: function () {
          return [
            {
              name: '1',
              value: 100
            },
            {
              name: '2',
              value: 370
            }
          ]
        }
      }
    },
    data: function () {
      return {
        // 
        axisColor: '#797979',
        // 
        splitLineColor: '#dcdcdc'
      }
    },
    computed: {
      // id, 
      id: function () {
        return parseInt(Math.random() * 1000000)
      },
      legendData: function () {
        let legendData = []
        this.series.forEach(function (elem) {
          legendData.push(elem.name)
        })
        return legendData
      }
    },
    mounted: function () {
      // echart
      var chartPie = echarts.init(document.getElementById(this.id))
      // eChart (http://echarts.baidu.com/option.html#yAxis.splitLine.lineStyle.color)
      chartPie.setOption({
        title: { text: this.title },
        grid: {
          left: 30,
          right: 15
        },
        color: this.color,
        tooltip: {},
        series: {
          type: 'pie',
          data: this.data
        }
      })
    }
  }
</script>



