<template>
  <div class="vm-chart-bar vm-panel">
    <div class="panel-body" :id="this.id" :style="{ height: height + 'px'}">
    </div>
  </div>
</template>
<script>
  import chartTheme from '@/theme/chartTheme.js'
  //  ECharts 
  var echarts = require('echarts/lib/echarts')
  // 
  require('echarts/lib/chart/bar')
  require('echarts/lib/chart/line')
  // 
  require('echarts/lib/component/tooltip')
  require('echarts/lib/component/title')
  require('echarts/lib/component/legend')

  export default {
    name: 'VmChartBarLine',
    props: {
      // 
      title: {
        type: String,
        default: ''
      },
      height: {
        type: Number,
        default: 400
      },
      // , ecahrt
      color: {
        type: Array,
        default: function () {
          return chartTheme.color
        }
      },
      // 
      bgColor: {
        type: String,
        default: '#fff'
      },
      // 
      xAxisData: {
        type: Array,
        required: true,
        default: function () {
          return ['', '', '', '', '', '']
        }
      },
      // 
      series: {
        type: Array,
        required: true,
        default: function () {
          return [{
            name: '',
            type: 'bar',
            data: [5, 20, 36, 10, 10, 20]
          }]
        }
      }
    },
    data: function () {
      return {
        // 
        axisColor: {
          type: String,
          default: '#797979'
        },
        // 
        splitLineColor: {
          type: String,
          default: '#dcdcdc'
        }
      }
    },
    computed: {
      // id, 
      id: function () {
        return parseInt(Math.random() * 1000000)
      },
      legendData: function () {
        let legendData = []
        this.series.forEach(function (elem) {
          legendData.push(elem.name)
        })
        return legendData
      }
    },
    mounted: function () {
      // echart
      var chartBar = echarts.init(document.getElementById(this.id))
      // eChart (http://echarts.baidu.com/option.html#yAxis.splitLine.lineStyle.color)
      chartBar.setOption({
        title: { text: this.title },
        legend: {
          icon: 'circle',
          data: this.legendData,
          bottom: 0
        },
        grid: {
          left: 30,
          right: 15
        },
        color: this.color,
        tooltip: {},
        xAxis: {
          data: this.xAxisData,
          axisLine: {
            lineStyle: {
              color: this.axisColor
            }
          }
        },
        yAxis: {
          axisLine: {
            lineStyle: {
              color: this.axisColor
            }
          },
          splitLine: {
            lineStyle: {
              color: '#dcdcdc'
            }
          }
        },
        series: this.series
      })
    }
  }
</script>



